<template>
  <div class="p-l-r-20 p-b-6">
    <el-row :gutter="20">
      <el-col class="el-col-auto-span">
        <el-form-item
          ref="subAgentExpansionModelTwo"
          :label="$t('IDC.organization.subAgentExpansionModel')"
          prop="subAgentExpansionModelTwo"
          required
        >
          <div v-if="isViewMode">
            <span v-text="getSubAgentModelName(assignInfo.subAgentExpansionModelTwo)" />
          </div>
          <el-select
            v-else
            id="subAgentExpansionModelTwo"
            v-model="assignInfo.subAgentExpansionModelTwo"
            :placeholder="$t('common.placeholder.select')"
          >
            <el-option
              v-for="option in subAgentExpansionModelOptions"
              :key="option.value"
              :value="option.value"
              :label="option.label"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'OrgHierachy',
  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    parentIdentityModel: {
      type: String,
      default: '',
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      subAgentExpansionModelOptions: [],
    };
  },
  methods: {
    getModelName(val) {
      switch (val) {
        case '0000':
          return 'Can Make Aggregator';
        case '0001':
          return 'Can Make Owned';
        case '0002':
          return 'Can Make AllType';
        case '0003':
          return 'NoneHierarchy';
        default:
          return;
      }
    },
    getSubAgentModelName(val) {
      switch (val) {
        case '0000':
          return 'Can Make Aggregator';
        case '0001':
          return 'Can Make Owned';
        case '0002':
          return 'Can Make AllType';
        case '0003':
          return 'NoneHierarchy';
        default:
          return;
      }
    },
  },
  watch: {
    'assignInfo.products': {
      handler(products) {
        products = products || [];
        const noneHierarchyCount = products.filter(
          (product) => product.productProperties.ORG_IDENTITY_MODEL === '3'
        ).length;
        const ownedCount = products.filter((product) => product.productProperties.ORG_IDENTITY_MODEL === '1').length;
        const aggregatorCount = products.filter(
          (product) => product.productProperties.ORG_IDENTITY_MODEL === '0'
        ).length;
        // 计算所选产品的identityModel
        let identityModel = '';
        let subAgentExpansionModelOptions = [];
        if (noneHierarchyCount === products.length) {
          identityModel = '0003';
        } else if (ownedCount > 0 && aggregatorCount > 0) {
          identityModel = '0002';
        } else if (ownedCount > 0) {
          identityModel = '0001';
        } else {
          identityModel = '0000';
        }
        this.assignInfo.identityModel = identityModel;

        if (identityModel === '0001') {
          subAgentExpansionModelOptions = [
            {
              value: '0003',
              label: 'None',
            },
            {
              value: '0000',
              label: 'Aggregator',
            },
            {
              value: '0001',
              label: 'Owned',
            },
            {
              value: '0002',
              label: 'Owned & Aggregator',
            },
          ];
        } else {
          subAgentExpansionModelOptions = [
            {
              value: '0000',
              label: 'Aggregator',
            },
            {
              value: '0003',
              label: 'None',
            },
          ];
        }
        this.subAgentExpansionModelOptions = subAgentExpansionModelOptions;
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>
