<template>
  <div class="subscripb-product">
    <simple-collapse class="panel-collapse m-b-16" :title="$t('IDC.organization.product')">
      <el-divider style="margin: 0 0 10px" />
      <div class="sub-block p-b-6">
        <div class="tips m-b-16" v-if="!isViewMode">
          Subscribe to products for the organization. The trust level determines the products, in addition to basic
          products, to which the organization can subscribe.
        </div>
        <el-button
          class="m-b-16"
          icon="el-icon-plus"
          size="small"
          type="primary"
          plain
          v-if="!isViewMode"
          @click="addScribeProductDialogVisible = true"
        >
          {{ $t('common.button.add') }}
        </el-button>
        <div class="sub-block__fields">
          <div class="p-b-18">
            <!-- product table -->
            <org-product-table
              v-loading="prodLoading"
              :columns="subscribeProductsColumns"
              :outSideTableData="assignInfo.products"
              :maxHeight="280"
            >
              <!-- effectiveDate 插槽-->
              <template slot="effectiveTime" slot-scope="scope">
                <el-form
                  :model="scope.data.row"
                  ref="effectiveDate"
                  :rules="{
                    effectiveDate: {
                      required: true,
                      message: 'Please enter content',
                      trigger: 'blur',
                    },
                  }"
                  label-position="top"
                  size="small"
                >
                  <span v-if="isViewMode" v-text="scope.data.row.effectiveDate"></span>
                  <el-form-item prop="effectiveDate" v-else>
                    <el-date-picker
                      v-model="scope.data.row.effectiveDate"
                      :value-format="DATE_FORMAT.COMP_VALUE"
                      :format="DATE_FORMAT.COMP_DISPLAY"
                      type="date"
                      size="small"
                      :placeholder="$t('common.placeholder.enter')"
                      style="width: 100%"
                      :picker-options="
                        pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                      "
                    ></el-date-picker>
                  </el-form-item>
                </el-form>
              </template>

              <!-- expiredTime 插槽-->
              <template slot="expireTime" slot-scope="scope">
                <el-form
                  :model="scope.data.row"
                  ref="expireDate"
                  :rules="{
                    expireDate: {
                      required: true,
                      message: 'Please enter content',
                      trigger: 'blur',
                    },
                  }"
                  label-position="top"
                  size="small"
                >
                  <span v-if="isViewMode" v-text="scope.data.row.expireDate"></span>
                  <el-form-item prop="expireDate" v-else>
                    <el-date-picker
                      v-model="scope.data.row.expireDate"
                      :value-format="DATE_FORMAT.COMP_VALUE"
                      :format="DATE_FORMAT.COMP_DISPLAY"
                      type="date"
                      size="small"
                      :placeholder="$t('common.placeholder.enter')"
                      style="width: 100%"
                      :picker-options="
                        pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                      "
                    ></el-date-picker>
                  </el-form-item>
                </el-form>
              </template>

              <!-- chargeProfile 插槽 -->
              <template slot="chargeProfile" slot-scope="scope">
                <span v-if="isViewMode">
                  {{ scope.data.row | productChargeProfile }}
                </span>
                <el-select
                  v-model="scope.data.row.chargeProfileId"
                  placeholder="Please select"
                  v-if="scope.data.row.chargeProfiles && scope.data.row.chargeProfiles.length > 0 && !isViewMode"
                  class="charge-profile-select"
                >
                  <el-option
                    v-for="item in scope.data.row.chargeProfiles"
                    :key="item.chargeProfileId"
                    :label="item.chargeProfileName"
                    :value="item.chargeProfileId"
                  >
                  </el-option>
                </el-select>
              </template>

              <!-- operation 插槽 -->
              <template slot="operation" slot-scope="scope">
                <el-button
                  type="text"
                  size="small"
                  v-if="scope.data.row.isProduct > 0"
                  @click="() => deletNewProductFromSelected(scope.data.$index)"
                  >Delete</el-button
                >
              </template>
            </org-product-table>
          </div>
        </div>
      </div>
    </simple-collapse>

    <simple-collapse
      class="panel-collapse m-b-16"
      title="Bill Reference Number Validation"
      v-if="assignInfo.showBillReferenceNumberValidation"
    >
      <el-divider style="margin: 0 0 10px" />
      <div>
        <!-- validationMode -->
        <el-row :gutter="20">
          <el-col class="el-col-auto-span">
            <el-form-item label="Validation Mode" prop="validationMode">
              <span v-text="getValidationMode(assignInfo.validationMode)" v-if="isViewMode" />
              <el-select
                v-else
                v-model="assignInfo.validationMode"
                :placeholder="$t('common.placeholder.select')"
                size="small"
              >
                <el-option
                  v-for="(item, index) in validationModeOptions"
                  :value="item.value"
                  :label="item.label"
                  :key="index"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col class="el-col-auto-span" v-if="showRegularExpression">
            <el-form-item label="Regular Expression" prop="validationExpression" required>
              <span v-if="isViewMode" v-text="assignInfo.validationExpression" />
              <el-input
                v-model="assignInfo.validationExpression"
                :placeholder="$t('common.placeholder.enter')"
                size="small"
                v-else
              />
            </el-form-item>
          </el-col>
        </el-row>

        <!-- allowPartialPayement -->
        <el-row :gutter="20">
          <el-col class="el-col-auto-span">
            <el-form-item label="Allow Partial Bill Payment">
              <span v-text="assignInfo.allowPartialPayement == '1' ? 'YES' : 'NO'" v-if="isViewMode" />
              <div v-else>
                <el-radio v-model="assignInfo.allowPartialPayement" :label="1" size="small">YES</el-radio>
                <el-radio v-model="assignInfo.allowPartialPayement" :label="0" size="small">NO</el-radio>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </simple-collapse>

    <!-- 添加product弹窗 -->
    <product-dialog
      :productVisible.sync="addScribeProductDialogVisible"
      :addForm="productPopupConf"
      :isCheck="true"
      :isCheckMemory="true"
      :hasPagination="false"
      :showPageSize="false"
      :pageSizes="[10]"
      :childSearch="loadProductParam"
      @submit-call-back="onProductChange"
    ></product-dialog>
  </div>
</template>

<script>
import ProductDialog from '@/views/identifier-center/pages/customer/create/components/product-dialog';
import OrgProductTable from '@/views/identifier-center/pages/organization/detail/components/org-product-table';
import SimpleCollapse from '../components/simple-collapse.vue';
import { formatStrToDate3 } from '@/common/utils/date-time-utils';
import { queryAssignProduct } from '@/views/identifier-center/api/api-organization.js';
import dayjs from 'dayjs';

const DATE_FORMAT = {
  COMP_DISPLAY: 'dd/MM/yyyy',
  COMP_VALUE: 'dd/MM/yyyy',
  DAYJS: 'DD/MM/YYYY',
};
export default {
  name: 'orgCreateSubsribeProduct',
  components: { OrgProductTable, ProductDialog, SimpleCollapse },

  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
    identityInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      DATE_FORMAT,
      addScribeProductDialogVisible: false,
      prodLoading: false,
      rules: {
        dateRule: [{ required: true, message: 'Please enter context', trigger: 'blur' }],
        effectiveRules: [],
        expiryRules: [], // 暂未实现
      },
      productPopupConf: {
        title: 'Add Product',
        label: 'Product Name',
        idKey: 'productId',
        columns: [
          { label: 'Product Name', prop: 'productName', align: 'left' },
          { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left' },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: 'Product Name',
              key: 'name',
              prop: 'name',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      validationModeOptions: [
        {
          label: 'External Validation',
          value: '2',
        },
        {
          label: 'Internal and External',
          value: '3',
        },
        {
          label: 'Internal Validation',
          value: '1',
        },
        {
          label: 'No Validation',
          value: '0',
        },
      ],
    };
  },
  computed: {
    showRegularExpression() {
      return (
        this.assignInfo.validationMode &&
        this.assignInfo.validationMode !== '0' &&
        this.assignInfo.validationMode !== '2'
      );
    },
    subscribeProductsColumns() {
      const columns = [
        { label: 'Product Name', prop: 'productName', align: 'left', minWidth: 160 },
        { label: 'Business Operations Group', prop: 'productOrgRoleNames', align: 'left', minWidth: 160 },
        {
          label: this.$t('IDC.organization.table.effectiveDate'),
          prop: 'effectiveDate',
          align: 'left',
          __slotName: 'effectiveTime',
          width: 220,
        },
        {
          label: this.$t('IDC.organization.table.expiryDate'),
          prop: 'expireDate',
          align: 'left',
          __slotName: 'expireTime',
          width: 220,
        },
        {
          label: this.$t('IDC.organization.chargeProfile'),
          prop: 'chargeProfileId',
          align: 'left',
          __slotName: 'chargeProfile',
          width: 220,
        },
        { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left', width: 200 },
      ];
      if (!this.isViewMode) {
        columns.push({
          label: this.$t('common.label.operation'),
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
          width: 100,
        });
      }
      return columns;
    },
  },

  filters: {
    productChargeProfile: function (value = {}) {
      if (value.chargeProfiles) {
        let arr = value.chargeProfiles.filter((data) => data.chargeProfileId == value.chargeProfileId);
        if (arr.length > 0) {
          return arr[0].chargeProfileName;
        } else {
          return '';
        }
      } else {
        return '';
      }
    },
  },
  methods: {
    pickerOptionsEffectiveDate(data, data2 = '') {
      return {
        disabledDate: (time) => {
          let startDateVal = data;
          let endDateVal = data2;
          if (endDateVal) {
            return (
              time.getTime() < dayjs(startDateVal, DATE_FORMAT.DAYJS).valueOf() ||
              time.getTime() > dayjs(endDateVal, DATE_FORMAT.DAYJS).valueOf()
            );
          } else {
            return time.getTime() < dayjs(startDateVal, DATE_FORMAT.DAYJS).valueOf();
          }
        },
      };
    },
    getValidationMode(val) {
      switch (val) {
        case '0':
          return 'No Validation';
        case '1':
          return 'Internal Validation';
        case '2':
          return 'External Validation';
        case '3':
          return 'Internal and External';
      }
    },
    removeSubscribeProduct(index) {
      if (index !== 0 && !index) {
        return;
      }
      const product = this.assignInfo.subscribeProducts[index];
      if (!product) {
        return;
      }
      if (!product.isNewItem) {
        this.$modal.msgWarning('not allowd remove origin product');
        return;
      }

      this.$modal
        .confirm(this.$i18n.t('common.tips.delete'), '', {
          confirmButtonText: this.$i18n.t('common.button.confirm'),
          cancelButtonText: this.$i18n.t('common.button.cancel'),
        })
        .then(() => {
          this.assignInfo.subscribeProducts.splice(index, 1);
        });
    },

    // 点击弹窗 confirm按钮调用
    onProductChange(rows) {
      rows.forEach((item) => {
        let obj = Object.assign({}, item);
        obj.isProduct = 1;
        this.assignInfo.products.push(obj);
      });
      if (rows.length > 0) {
        this.$emit('refresh-accountType', {});
      }
    },

    // 删除产品操作
    deletNewProductFromSelected(index) {
      this.$confirm('Are you sure to delete it?', '', {
        confirmButtonText: this.$t('common.button.delete'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'warning',
        customClass: 'delete-confirm',
      }).then(() => {
        this.assignInfo.products.splice(index, 1);
        this.$emit('refresh-accountType', {});
      });
    },

    // 点击添加按钮调用
    async loadProductParam(param) {
      return new Promise((resolve) => {
        let params = {
          page: param.pageNo || 1,
          segmentIds: this.assignInfo.segmentIds || [],
          trustLevelId: this.assignInfo.trustLevelId,
          basicProductUniqueId: this.assignInfo.basicProductUniqueId,
          selectProducts: this.assignInfo.products.map((item) => item.productId),
          identitySubType: 5002,
        };
        if (param.searchKey) {
          params.productName = param.searchKey.name;
        }
        queryAssignProduct(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products.map((item) => {
                item.effectiveDateInit = item.effectiveDate;
                item.expireDateInit = item.expireDate;
                return { ...item };
              });
              if (res.page) {
                res.total = res.page.totalSize;
              }
            }
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {});
      });
    },

    pickerOptionsEnd(row = {}) {
      return {
        disabledDate(time) {
          if (row.effectiveTime) {
            return time.getTime() > Date.now || time.getTime() < formatStrToDate3(row.effectiveTime).getTime();
          } else {
            return time.getTime() <= Date.now();
          }
        },
      };
    },
    pickerProdTimeStart(row = {}) {
      return {
        disabledDate(time) {
          if (row.expiredTime) {
            return (
              time.getTime() < formatStrToDate3(row.originalEffectiveTime).getTime() - 8.64e7 ||
              time.getTime() > formatStrToDate3(row.originalExpiredTime).getTime() - 8.64e7
            );
          } else {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
      };
    },
    pickerProdTimeEnd(row = {}) {
      return {
        disabledDate(time) {
          if (row.expiredTime) {
            if (time.getTime() > formatStrToDate3(row.originalExpiredTime).getTime()) {
              return true;
            }
            if (time.getTime() < formatStrToDate3(row.originalEffectiveTime).getTime()) {
              return true;
            }
          } else {
            return time.getTime() <= Date.now();
          }
        },
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.subscripb-product {
  /deep/ .el-table__row {
    .el-table__cell {
      vertical-align: top;
      padding-top: 12px;
      padding-bottom: 12px;
      .cell {
        min-height: 32px;
        line-height: 32px;
      }
    }
    .el-form-item {
      margin-bottom: 0;
    }
    .el-form-item.is-error {
      margin-top: 0px;
      margin-bottom: 24px;
      position: relative;
      .el-form-item__error {
        width: 100%;
        padding-top: 8px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: break-all;
      }
    }
  }

  /deep/ .charge-profile-select {
    .el-select__caret {
      line-height: 32px;
    }
  }
}
</style>
