<template>
  <div class="p-l-r-20 p-b-6">
    <el-row :gutter="20">
      <!-- Charge Profile -->
      <el-col :span="8">
        <el-form-item prop="chargeProfile">
          <template v-slot:label>
            <div class="ruleProfile-label">
              <span>Charge Profile</span>
              <span v-if="!isViewMode" class="label-default" @click="chargeProfileUseDefault">Use Default</span>
            </div>
          </template>
          <div v-if="isViewMode">
            <span v-text="selectChargeProfileName" />
          </div>
          <el-select
            v-else
            v-model="assignInfo.selectChargeProfileId"
            placeholder="Please select"
            @change="chargeProfileChange"
            clearable
          >
            <el-option
              v-for="item in assignInfo.chargeProfileList"
              :key="item.chargeProfileId"
              :label="item.chargeProfileName"
              :value="item.chargeProfileId"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item prop="selectedRuleProfileId" required>
          <template v-slot:label>
            <div class="ruleProfile-label">
              <span>Rule Profile</span>
              <span v-if="!isViewMode" class="label-default" @click="ruleProfileUseDefault">Use Default</span>
            </div>
          </template>
          <div v-if="isViewMode">
            <span v-text="selectedRuleProfileName" />
          </div>
          <el-select
            v-else
            v-model="assignInfo.selectedRuleProfileId"
            @change="ruleProfileChange"
            placeholder="Please select"
          >
            <el-option
              v-for="item in assignInfo.ruleProfileList"
              :key="item.ruleProfileId"
              :label="item.ruleProfileName"
              :value="item.ruleProfileId"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
const PROFILE_FIELD = {
  CHARGE: 'CHARGE_PROFILE',
  RULE: 'RULE_PROFILE',
};

export default {
  name: 'orgCreateProfile',
  components: {},

  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      PROFILE_FIELD,
    };
  },

  computed: {
    selectChargeProfileName() {
      if (this.assignInfo.chargeProfileList) {
        if (this.assignInfo.chargeProfileList.length > 0) {
          return this.assignInfo.chargeProfileList.filter(
            (item) => this.assignInfo.selectChargeProfileId == item.chargeProfileId
          )[0].chargeProfileName;
        } else {
          return '';
        }
      } else {
        return '';
      }
    },
    selectedRuleProfileName() {
      if (this.assignInfo.ruleProfileList) {
        if (this.assignInfo.ruleProfileList.length > 0) {
          return this.assignInfo.ruleProfileList.filter(
            (item) => this.assignInfo.selectedRuleProfileId == item.ruleProfileId
          )[0].ruleProfileName;
        } else {
          return '';
        }
      } else {
        return '';
      }
    },
  },

  created() {},

  methods: {
    ruleProfileChange(id) {
      this.assignInfo.selectedRuleProfileId = id;
      this.$forceUpdate();
    },
    chargeProfileChange(id) {
      this.assignInfo.selectChargeProfileId = id;
      this.$forceUpdate();
    },
    chargeProfileUseDefault() {
      this.$emit('use-default', 'charge');
    },
    ruleProfileUseDefault() {
      this.$emit('use-default', 'rule');
    },
    // 设置DefalutProfile
    setDefalutProfile(fieldName) {
      if (!fieldName) {
        return;
      }
      switch (fieldName) {
        case PROFILE_FIELD.CHARGE:
          this.assignInfo.selectChargeProfileId = this.assignInfo.defaultRuleProfileId;
          break;
        case PROFILE_FIELD.RULE:
          this.assignInfo.selectedRuleProfileId = this.assignInfo.defaultRuleProfileId;
          break;
      }
    },
    getOptionsLabelByValue(list, val) {
      return (list.find((item) => item.value === val) || {}).label || '-';
    },
  },
};
</script>

<style lang="scss" scoped>
/deep/ .el-form-item__label {
  width: 100%;
}
.ruleProfile-label {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-right: 12px;
  box-sizing: border-box;
  .label-default {
    cursor: pointer;
    color: $--primary_color;
  }
}
</style>
