<template>
  <div>
    <div class="sub-block p-l-r-20 p-b-6">
      <div class="tips m-b-16" v-if="!isViewMode">{{ $t('IDC.organization.addBankTips') }}</div>
      <el-button
        class="m-b-16"
        icon="el-icon-plus"
        size="small"
        type="primary"
        plain
        v-if="!isViewMode"
        @click="addBankDialogVisible = true"
      >
        {{ $t('common.button.add') }}
      </el-button>
      <div class="sub-block__fields">
        <div class="p-b-18">
          <table-page
            size="small"
            :isSingleCheck="false"
            :has-pagination="false"
            :columns="bankAccountColumns"
            :outSideTableData="assignInfo.bankAccounts"
          >
            <template slot-scope="scope" slot="operation" v-if="!isViewMode">
              <el-button type="text" @click="removeBankCount(scope.data.$index)">
                {{ $t('common.button.delete') }}</el-button
              >
            </template>
          </table-page>
        </div>
      </div>
    </div>

    <!-- 创建弹框 -->
    <portal-dialog
      v-if="!isViewMode"
      :visible.sync="addBankDialogVisible"
      :title="$t('IDC.organization.addBankAccount')"
      :confirmText="$t('common.button.confirm')"
      :width="'800px'"
      :height="'442px'"
      @cancel-btn-click="closeDialog"
      @confirm-btn-click="addBanksubmit"
    >
      <template #content>
        <BankAccountDialog ref="bankAccountDialog" :createBankAccountForm="createBankAccountForm"></BankAccountDialog>
      </template>
    </portal-dialog>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalDialog from '@/components/portal-dialog';
import BankAccountDialog from '@/views/identifier-center/pages/bank-account/create/bank-account-dialog';

export default {
  name: 'OrgBankAccount',
  components: { TablePage, PortalDialog, BankAccountDialog },

  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
    identityInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      addBankDialogVisible: false,

      createBankAccountForm: {
        assetType: '',
        accountName: '',
        accountNo: '',
        bankHeadOffice: '',
        bankId: '',
        currency: '',
        transferMode: '',
        iban: '',
        validityPeriod: '',
        effectiveDate: '',
        expiryDate: '',
      },
    };
  },

  computed: {
    bankAccountColumns() {
      const columns = [
        { label: this.$t('IDC.bankAccount.label.bank'), prop: 'bankHeadOfficeName', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.bankBranch'), prop: 'bankName', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.bankAccountName'), prop: 'accountName', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.bankAccountNumber'), prop: 'accountNo', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.currency'), prop: 'currencyName', align: 'left' },
      ];
      if (!this.isViewMode) {
        columns.push({ label: this.$t('IDC.auditlog.operation'), align: 'left', __slotName: 'operation' });
      }
      return columns;
    },
  },

  created() {},

  methods: {
    removeBankCount(index) {
      if (index !== 0 && !index) {
        return;
      }
      this.$modal
        .confirm(this.$i18n.t('common.tips.delete'), '', {
          confirmButtonText: this.$i18n.t('common.button.confirm'),
          cancelButtonText: this.$i18n.t('common.button.cancel'),
        })
        .then(() => {
          this.assignInfo.bankAccounts.splice(index, 1);
        });
    },
    addBanksubmit() {
      this.$refs.bankAccountDialog.$refs.createBankAccountForm.validate((valid) => {
        if (!valid) {
          return;
        }

        const bankHeadOfficeOptions = this.$refs.bankAccountDialog.bankHeadOfficeOptions;
        const bankOptions = this.$refs.bankAccountDialog.bankOptions;
        const currencyOptions = this.$refs.bankAccountDialog.currencyOptions;

        const bankHeadOfficeId = this.createBankAccountForm.bankHeadOffice;
        const bankId = this.createBankAccountForm.bankId;
        const currencyId = this.createBankAccountForm.currency;

        const bankHeadOfficeName =
          (bankHeadOfficeOptions.find((item) => item.value === bankHeadOfficeId) || {}).label || bankHeadOfficeId;
        const bankName = (bankOptions.find((item) => item.value === bankId) || {}).label || bankId;
        const currencyName = (currencyOptions.find((item) => item.value === currencyId) || {}).label || currencyId;

        const bankAccount = {
          bankHeadOffice: bankHeadOfficeId,
          bankId,
          currency: currencyId,
          bankHeadOfficeName,
          bankName,
          currencyName,
          accountName: this.createBankAccountForm.accountName,
          accountNo: this.createBankAccountForm.accountNo,
          createOperId: '-1',
          identityId: this.identityInfo.parentId,
          assetType: this.createBankAccountForm.assetType,
          transferMode: this.createBankAccountForm.transferMode, // 新增
          iban: this.createBankAccountForm.iban, // 新增
          effectiveDate: this.createBankAccountForm.validityPeriod ? this.createBankAccountForm.validityPeriod[0] : '',
          expiryDate: this.createBankAccountForm.validityPeriod ? this.createBankAccountForm.validityPeriod[1] : '',
        };
        this.assignInfo.bankAccounts.push(bankAccount);
        this.addBankDialogVisible = false;
        // reset fields
        this.$refs.bankAccountDialog.$refs.createBankAccountForm.resetFields();
      });
    },
    closeDialog() {
      this.addBankDialogVisible = false;
    },
  },
};
</script>
