<template>
  <div class="panel">
    <el-form
      label-position="top"
      :model="profileFormTemp"
      ref="commissionForm"
      class="settlement-form"
      :rules="commissionRules"
    >
      <div class="content-wrapper">
        <!-- form items config -->
        <!-- Commission Settlement Configurationg -->
        <div class="sub-block">
          <el-row :gutter="20">
            <el-col :span="8" v-if="isCanMakeAggergator">
              <el-form-item
                :label="$t('IDC.revenueSettlementInfo.commissionDistributionModel')"
                prop="subAgentExpansionModel"
              >
                <span
                  v-text="getLableToOptions(profileFormTemp.subAgentExpansionModel, distributionModelList)"
                  v-if="!isEditModel"
                />

                <el-select
                  v-else
                  v-model="profileFormTemp.subAgentExpansionModel"
                  placeholder="Please select"
                  size="small"
                >
                  <el-option
                    v-for="item in distributionModelList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                    @change="distributionModelChange"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item
                :label="$t('IDC.revenueSettlementInfo.commissionSettlementTemplate')"
                prop="subSettlementConfigTemplate"
              >
                <span
                  v-if="!isEditModel"
                  v-text="getLableToOptions(profileFormTemp.subSettlementConfigTemplate, settlementTemplateList)"
                />
                <el-select
                  v-else
                  v-model="profileFormTemp.subSettlementConfigTemplate"
                  placeholder="Please select"
                  size="small"
                  @change="commissionTemplateChange"
                >
                  <el-option
                    v-for="item in settlementTemplateList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-form>
  </div>
</template>
<script>
import {
  queryOrgRevenueTemplateNames,
  queryCommissionCfg,
} from '@/views/identifier-center/api/api-revenue-settlement.js';
import { IDENTITY_MODEL } from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import { deepClone } from '@/views/identifier-center/pages/organization/utils/common';
import { dealApiRes, getbatchData, transOptions } from '@/views/identifier-center/pages/organization/utils/common.js';
import { getCommissionInfoRules } from '@/views/identifier-center/validator/revenue-settlement-info.js';
const TEMPLARW_TYPES = {
  AGREEMENT: '1',
  CONFIG: '2',
};
export default {
  name: 'RevenueSettlementInfo',
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
    profileFormTemp: {
      type: Object,
      default() {
        return {};
      },
    },
    panelActived: {
      type: Boolean,
      default: false,
    },
    isViewMode: {
      type: Boolean,
      default: false,
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      commissionRules: !this.isViewMode ? getCommissionInfoRules(this.profileFormTemp) : {},
      submitLoading: false,
      isEditModel: !this.isViewMode,
      distributionModelList: [],
      settlementTemplateList: [],
      profileTemplate: {
        subAgentExpansionModel: '',
        settlementConfigTemplate: {},
        subSettlementConfigTemplate: '',
      },
    };
  },
  watch: {
    panelActived: {
      handler(value) {
        if (value) {
          this.initPanel();
        }
      },
      immediate: true,
    },
    profileTemplate: {
      handler(value) {
        if (value) {
          this.$emit('update:profileFormTemp', deepClone(value));
        }
      },
      deep: true,
    },
    isViewMode(val) {
      this.isEditModel = !val;
    },
  },
  computed: {
    isCanMakeAggergator() {
      // 只有顶层组织可以修改加盟子组织分佣模型
      const isCanMakeAggergator = this.assignInfo.identityModel === IDENTITY_MODEL.CAN_MAKE_AGGREGATOR;
      return isCanMakeAggergator;
    },
  },
  methods: {
    async initPanel() {
      const result = await this.getAllInitData();
      if (!result) {
        return;
      }
      this.settlementCycleUnitList =
        result.settlementCycleUnitList && result.settlementCycleUnitList.map(transOptions('key', 'value'));
      // Commission Distribution Model---列表
      result.commissionCfgModelList &&
        result.commissionCfgModelList.forEach((item) => {
          this.distributionModelList.push({
            label: item.name,
            value: item.templateId,
          });
        });
      // Commission Settlement Template---列表
      result.commissionCfgTemplateList &&
        result.commissionCfgTemplateList.forEach((item) => {
          item.label = item.name;
          item.value = item.templateId;
          this.settlementTemplateList.push(item);
        });
    },

    async getAllInitData() {
      try {
        const result = await getbatchData({
          profileMap: dealApiRes(queryOrgRevenueTemplateNames, 'revenueTemplateNameMap', {}),
          commissionCfgModelList: dealApiRes(queryCommissionCfg, 'commissionConfigs', {
            templateType: TEMPLARW_TYPES.AGREEMENT,
            pageSize: null,
            pageNum: null,
          }),
          commissionCfgTemplateList: dealApiRes(queryCommissionCfg, 'commissionConfigs', {
            templateType: TEMPLARW_TYPES.CONFIG,
            pageSize: null,
            pageNum: null,
          }),
        });
        return result;
      } catch (error) {
        return null;
      }
    },
    distributionModelChange(val) {
      this.profileFormTemp.subAgentExpansionModel = val;
    },
    commissionTemplateChange(val) {
      let settlementConfigTemplate = this.settlementTemplateList.find((item) => item.templateId === val);
      this.profileFormTemp.settlementConfigTemplate = settlementConfigTemplate;
      this.profileFormTemp.subSettlementConfigTemplate = val;
    },
    getLableToOptions(val, list) {
      if (!val) {
        return '-';
      }

      if (!list || !list.length) {
        return '-';
      }

      if (val === 'Please Select') {
        return '-';
      }
      const curName = list.find((item) => item.value === val) || {};
      return curName.label || '-';
    },
    setEditModel(val) {
      this.isEditModel = val;
    },
    resetFields() {
      this.$refs.revenueForm.resetFields();
    },
    validate() {
      return new Promise((resolve) => {
        this.$refs['commissionForm'] &&
          this.$refs['commissionForm'].validate((valid) => {
            return resolve(valid);
          });
      });
    },
  },
  mounted() {},
};
</script>
