  padding: 20px;

  .settlement-form {
    /deep/ {
      .el-form-item__label {
        color: $--text_color--secondary;
      }
      .el-form-item__content {
        color: $--text_color--strong;
        line-height: 32px;
      }
      .remark {
        margin-top: 20px;
      }
    }
  }
}

.sub-block {
  &__title {
    position: relative;
    color: $--text_color--strong;
    font-weight: bold;
    text-indent: 6px;
    margin: 20px 0 20px;

    &::before {
      content: '';
      width: 2px;
      height: 16px;
      display: block;
      position: absolute;
      background-color: $--primary_color;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__title--normal {
    text-indent: 0;

    &::before {
      display: none;
    }
  }
}
