<template>
  <div class="sub-block p-l-r-20 p-b-6">
    <div class="tips m-b-16" v-if="!isViewMode">
      Configure the account rule profile for each account of an organization. An account rule profile is a package of
      restrictions specified for an identity account.
    </div>
    <div class="sub-block__fields">
      <div class="p-b-18">
        <table-page
          size="small"
          :isSingleCheck="false"
          :has-pagination="false"
          :showOverflowTooltip="false"
          :columns="accountColumns"
          :maxHeight="280"
          :outSideTableData="accountAssignInfo.accountTypeRuleProfileBeanList | accountTypeRuleProfileBeanList"
        >
          <!-- ruleProfile插槽 -->
          <template slot-scope="scope" slot="ruleProfile">
            <span v-if="isViewMode && scope.data.row.ruleProfileBeans.length > 0">{{
              scope.data.row | accountRlueProfile
            }}</span>
            <el-select
              v-model="scope.data.row.ruleProfileId"
              placeholder="please select"
              size="small"
              v-if="scope.data.row.ruleProfileBeans.length > 0 && !isViewMode"
            >
              <el-option
                v-for="item in scope.data.row.ruleProfileBeans"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </template>

          <!-- operation -->
          <template slot-scope="scope" slot="operation">
            <el-button
              type="text"
              @click="handleUseDefault(scope.data)"
              v-if="!isViewMode && scope.data.row.ruleProfileBeans.length > 0"
            >
              {{ 'Use Default' }}</el-button
            >
          </template>
        </table-page>
      </div>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';

export default {
  name: 'OrgCreateAccount',
  components: { TablePage },

  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    profileConfig: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {};
  },
  inject: ['accountAssignInfo'],

  computed: {
    accountColumns() {
      const columns = [
        { label: 'Account Type', prop: 'accountType.accountTypeName', align: 'left' },
        { label: 'Currency', prop: 'currency', align: 'left' },
        {
          label: 'RuleProfile',
          prop: 'name',
          align: 'left',
          __slotName: 'ruleProfile',
        },
      ];
      if (!this.isViewMode) {
        columns.push({ label: this.$t('common.label.operation'), align: 'left', __slotName: 'operation' });
      }
      return columns;
    },
  },

  created() {},
  filters: {
    accountRlueProfile: function (value = {}) {
      if (value.ruleProfileBeans) {
        return value.ruleProfileBeans.filter((data) => data.id == value.ruleProfileId)[0].name;
      } else {
        return '';
      }
    },
    accountTypeRuleProfileBeanList: function (value = {}) {
      return value.filter((data) => data.accountType.limitExclude === false);
    },
  },
  methods: {
    handleUseDefault(data) {
      let obj = data.row;
      obj.ruleProfileId = data.row.defaultRuleProfileId;
    },
    getOptionsLabelByValue(list, val) {
      return (list.find((item) => item.value === val) || {}).label || '-';
    },
  },
};
</script>
