<template>
  <div>
    <el-form :model="assignInfo" ref="assignInfo" :rules="assignRules" label-position="top" size="small">
      <!-- trustLevelInfo -->
      <simple-collapse class="panel-collapse m-b-16" title="Trust Level and Segment" name="trustLevelInfo">
        <el-divider />
        <div class="p-l-r-20 p-b-6">
          <el-row :gutter="20">
            <!-- trustLevel -->
            <el-col :span="8">
              <el-form-item prop="trustLevel" ref="trustLevel" label="Trust Level">
                <div>
                  <span v-text="`${assignInfo.trustLevel || ''}:${assignInfo.trustLevelName || ''}`" />
                </div>
              </el-form-item>
            </el-col>
            <!-- segment -->
            <el-col :span="8">
              <el-form-item prop="segment" ref="segment" label="Segment">
                <div>
                  <span v-text="`${assignInfo.selectSegmentName || '-'}`" />
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </simple-collapse>

      <!-- Select Product -->
      <org-create-subscribe-product
        :assignInfo="assignInfo"
        :isViewMode="isViewMode"
        @refresh-accountType="refreshAccountTypeEvn"
      ></org-create-subscribe-product>

      <!-- Profile -->
      <simple-collapse class="panel-collapse m-b-16" title="Profiile">
        <el-divider />
        <org-create-profile
          :assignInfo="assignInfo"
          :isViewMode="isViewMode"
          :identityInfo="identityInfo"
          :profileConfig="assignInfo.profileConfig"
          @use-default="useDefaulProfile"
        >
        </org-create-profile>
      </simple-collapse>

      <!-- Account -->
      <simple-collapse class="panel-collapse m-b-16" title="Account">
        <el-divider />
        <org-create-account :isViewMode="isViewMode" :profileConfig="assignInfo.profileConfig"> </org-create-account>
      </simple-collapse>

      <simple-collapse
        class="panel-collapse margin-bottom-20"
        :title="$t('IDC.organization.hierachy')"
        v-if="assignInfo.parentIdentityModel"
      >
        <el-divider />
        <org-create-hierachy
          :assignInfo="assignInfo"
          :isViewMode="isViewMode"
          :identityInfo="identityInfo"
        ></org-create-hierachy>
      </simple-collapse>

      <!-- Bank Account -->
      <simple-collapse class="panel-collapse m-b-16" :title="$t('IDC.organization.bankAccount')">
        <el-divider style="margin: 0 0 10px" />
        <org-create-bank-account
          :assignInfo="assignInfo"
          :isViewMode="isViewMode"
          :identityInfo="identityInfo"
        ></org-create-bank-account>
      </simple-collapse>

      <simple-collapse class="panel-collapse m-b-16" :title="$t('IDC.organization.bankAccount')">
        <el-divider style="margin: 0 0 10px" />
        <revenue-settlement-info
          ref="revenueSettlementInfo"
          :isViewMode="isViewMode"
          :panelActived="true"
          :pageType="pageType"
          :revenueFormData.sync="assignInfo.revenueFormData"
          :assignInfo="assignInfo"
          :isShowModel="isShowModel"
        ></revenue-settlement-info>
      </simple-collapse>
      <simple-collapse
        class="panel-collapse m-b-16"
        :title="$t('IDC.revenueSettlementInfo.commissionSettlementConfig')"
      >
        <el-divider class="divider-margin-10" />
        <commission-settlement-info
          ref="commissionSettlementInfo"
          :isViewMode="isViewMode"
          :panelActived="true"
          :assignInfo="assignInfo"
          :profileFormTemp.sync="assignInfo.profileFormTemp"
        ></commission-settlement-info>
      </simple-collapse>
    </el-form>
  </div>
</template>

<script>
import { getAssignInfoRules } from '@/views/identifier-center/validator/organization';
import SimpleCollapse from '../components/simple-collapse.vue';
import OrgCreateSubscribeProduct from './org-create-subscribe-product.vue';
import OrgCreateBankAccount from './org-create-bank-account.vue';
import OrgCreateProfile from './org-create-profile.vue';
import OrgCreateHierachy from './org-create-hierachy.vue';
import OrgCreateAccount from './org-create-account.vue';

import { getIdentityTypeEnum, getIdentitySubTypeEnum } from '@/views/identifier-center/api/api-organization.js';
import { transOptions, dealApiRes, getbatchData } from '@/views/identifier-center/pages/organization/utils/common.js';
import RevenueSettlementInfo from '../detail/components/revenue-settlement-info.vue';
import commissionSettlementInfo from '../detail/components/commission-settlement-info.vue';

export default {
  name: 'AssignInfo',
  components: {
    SimpleCollapse,
    OrgCreateSubscribeProduct,
    OrgCreateBankAccount,
    OrgCreateProfile,
    OrgCreateHierachy,
    OrgCreateAccount,
    RevenueSettlementInfo,
    commissionSettlementInfo,
  },

  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    isShowModel: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: 'child',
    },
    parentIdentityModel: {
      type: String,
      default: '',
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
    identityInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      assignRules: !this.isViewMode ? getAssignInfoRules() : {},
      pageType: 'create',
      rules: {
        dateRule: [{ required: true, message: 'Please enter context', trigger: 'blur' }],
        effectiveRules: [],
        expiryRules: [], // 暂未实现
      },
      // chargeProfile
      profileConfig: {
        chargeDefault: 'chargeDefault',
        ruleDefault: 'chargeDefault',
        chargeProfileList: [],
        ruleProfileList: [],
      },
    };
  },

  computed: {},

  created() {
    this.init();
  },

  methods: {
    useDefaulProfile(param) {
      if (param == 'charge') {
        this.assignInfo.selectChargeProfileId = this.assignInfo.defaultChargeProfileId;
      } else {
        this.assignInfo.selectedRuleProfileId = this.assignInfo.defaultRuleProfileId;
      }
    },
    getModelName(val) {
      switch (val) {
        case '0000':
          return 'Can Make Aggregator';
        case '0001':
          return 'Can Make Owned';
        case '0002':
          return 'Can Make AllType';
        case '0003':
          return 'NoneHierarchy';
        default:
          return;
      }
    },
    getSubAgentModelName(val) {
      switch (val) {
        case '0000':
          return 'Can Make Aggregator';
        case '0001':
          return 'Can Make Owned';
        case '0002':
          return 'Can Make AllType';
        case '0003':
          return 'NoneHierarchy';
        default:
          return;
      }
    },
    init() {
      // 防止 请求多次
      if (!this.isViewMode) {
        this.initProfile();
      }
    },
    refreshAccountTypeEvn() {
      this.$emit('refresh-accountType', {});
    },
    async initProfile() {
      const result = await this.getAllEnums();
      if (!result) {
        return;
      }

      let chargeProfileList = result.chargeProfileList;
      let ruleProfileList = result.ruleProfileList;

      if (chargeProfileList) {
        chargeProfileList = chargeProfileList.map(transOptions('id', 'chargeProfileName'));
        const chargeDefault = (chargeProfileList.find((item) => item.defaultRuleProfile) || {}).value;

        this.assignInfo.profileConfig.chargeDefault = chargeDefault;
        this.assignInfo.profileConfig.chargeProfileList = chargeProfileList;
      }
      if (ruleProfileList) {
        ruleProfileList = ruleProfileList.map(transOptions('id', 'ruleProfileName'));
        const ruleDefault = (ruleProfileList.find((item) => item.defaultRuleProfile) || {}).value;
        this.assignInfo.profileConfig.ruleDefault = ruleDefault;
        this.assignInfo.profileConfig.ruleProfileList = ruleProfileList;
      }
    },
    async getAllEnums() {
      try {
        const indentityDics = await this.getIndentityDicsCode();

        const ruleProfileParams = {
          identityType: indentityDics ? indentityDics.identityType : '',
          identitySubType: indentityDics ? indentityDics.identitySubType : '',
        };

        // 隐藏接口报错
        const result = await getbatchData({
          // chargeProfileList: dealApiRes(getChargeProfile, 'list'),
          // ruleProfileList: dealApiRes(getRuleProfile, 'list', ruleProfileParams),
        });
        return result;
      } catch (error) {}
      return null;
    },
    async getIndentityDicsCode() {
      try {
        const result = await getbatchData({
          identityType: dealApiRes(getIdentityTypeEnum, 'dics'),
          identitySubType: dealApiRes(getIdentitySubTypeEnum, 'dics'),
        });

        const identityTypeList = result.identityType;
        const identitySubTypeList = result.identitySubType;

        const identityType = (identityTypeList.find((item) => item.name === 'Organization') || {}).code;
        const identitySubType = (identitySubTypeList.find((item) => item.name === 'Child Organization') || {}).code;

        return {
          identityType,
          identitySubType,
        };
      } catch (error) {
        return null;
      }
    },

    validate() {
      return new Promise((resolve) => {
        this.$refs['assignInfo'].validate((valid) => {
          return resolve(valid);
        });
      });
    },
    resetFields() {
      this.$refs['assignInfo'].resetFields();
      // special fields
      this.assignInfo.bankAccount = [];
    },
  },
};
</script>

