<template>
  <div class="org-create create-page-content">
    <div v-show="!createCompleted">
      <!-- title -->
      <portal-header :projectObj="childTitleConfig" :showBottomLine="false" :lazy="true" />
      <div>
        <steps-tabs :activeName="activeName" :tabs-panel="tabsPanel" style="padding: 0">
          <template slot="first">
            <identity-info
              ref="identityTab"
              :type="type"
              :identityInfo.sync="identityInfo"
              :assignInfo.sync="assignInfo"
              :isViewMode="false"
              :kycInfo.sync="kycInfo"
              :hasDefaultParentShortCode="hasDefaultParentShortCode"
              v-if="identityInfo.orgNameRegex"
            ></identity-info>
            <div :style="styles" v-else v-loading="detaileLoading"></div>
          </template>
          <template slot="second">
            <assign-info
              ref="assignInfoTab"
              :type="type"
              :assignInfo.sync="assignInfo"
              :identityInfo="identityInfo"
              :isViewMode="false"
              @refresh-accountType="refreshAccountTypeEvn"
              v-if="activeName === 'second'"
            ></assign-info>
          </template>
          <template slot="third">
            <identity-info
              :identityInfo.sync="identityInfo"
              :assignInfo.sync="assignInfo"
              :kycInfo.sync="kycInfo"
              :type="type"
              :isViewMode="true"
            ></identity-info>
            <assign-info :type="type" :assignInfo.sync="assignInfo" :isViewMode="true"></assign-info>
          </template>
        </steps-tabs>
      </div>
      <footer-tap
        @reset="cancel"
        @previous="previous"
        @next="next"
        @submit="submit"
        :showCancel="true"
        :submit-loading="submitLoading"
        :active-name="activeName"
        :submitDisabled="!hasPerm('IC_IDENTITY_CREATESUBORG_NEXT_BTN')"
        :steps="tabsPanel"
      />
    </div>
    <div class="portal-success-container" v-show="createCompleted">
      <portal-create-success
        successText="Operation succeeded."
        @view-detail="goViewDetail"
        @continue-add="continueAdd"
      ></portal-create-success>
    </div>
  </div>
</template>
<script>
import PortalHeader from '@/components/portal-header';
import PortalCreateSuccess from '@/components/portal-create-success';
import StepsTabs from '@/components/stepsTabs';
import IdentityInfo from '../components/identity-info';
import AssignInfo from '../components/assign-info';
import {
  addOrganization,
  trustLevelAndSegment,
  checkInputKyc,
  queryAccountType,
  queryOrgNameRegex,
} from '@/views/identifier-center/api/api-organization.js';
import footerTap from '@/components/portal-footer-tap';

// 控制页签轮询
export default {
  name: 'CreateOrganization',
  components: {
    PortalCreateSuccess,
    PortalHeader,
    IdentityInfo,
    AssignInfo,
    StepsTabs,
    footerTap,
  },
  data() {
    return {
      cpId: '',
      kycInfo: {},
      organizationId: '',
      childTitleConfig: {
        title: this.$t('IDC.organization.childTitle'),
        desc: this.$t('IDC.organization.subTips'),
      },
      tabsPanel: [
        {
          name: 'first',
          title: 'Identify Info',
          subTitle: 'Enter your Identify Info',
        },
        {
          name: 'second',
          title: 'Enter Details',
          subTitle: 'Please Enter',
        },
        {
          name: 'third',
          title: 'Review',
          subTitle: 'Please Review',
        },
      ],
      identityInfo: {
        parentId: '',
        parentShortCode: '',
        parentOrgName: '',
        shortCode: '',
        merchName: '',
        productId: '',
        productName: '',
        description: '',
        notificationChannel: '',
        language: 'en',
        email: '',
        phoneNumber: '',
        simpleKYC: [],
        groupKYC: [],
        selectedProdList: [], // 选择的产品
        basicProductId: '',
        trustLevelName: '',
        orgNameRegex: '',
        defalutParentShortCode: '',
      },
      assignInfo: {
        parentIdentityModel: '',
        identityModel: '',
        subAgentExpansionModel: '',
        aggregatorModel: '',
        centralOwnedModel: '',
        chargeProfile: '',
        ruleProfile: '',
        bankAccounts: [],
        subscribeProducts: [],
        originProduct: [],
        selectedProduct: [],
        roleNum: '',
        segment: '',
        profileConfig: {
          chargeDefault: '',
          ruleDefault: '',
          chargeProfileList: [],
          ruleProfileList: [],
        },
        profileFormTemp: {
          settlementConfigTemplate: {},
          subAgentExpansionModel: '',
          subSettlementConfigTemplate: '',
        },
        revenueFormData: {
          profileName: '',
          profileId: 'Please Select',
          periodicSwitch: false,
          intervalValue: '',
          intervalTypeCode: '',
          startDate: '',
          endDate: '',
          manualWsSwitch: false,
          autoWsSwitch: false,
          settlementMinBalance: '',
          editableTabsValue: '',
          settlementProfileList: [],
          isShowModel: false,
        },

        // pay bill
        showBillReferenceNumberValidation: false,
        validationMode: '',
        allowPartialPayement: true,
        validationExpression: '',
        notifyText: '',
        accountTypeRuleProfileBeanList: [],
      },
      isShowModel: false,
      isShowPage: false,
      submitLoading: false,
      activeName: 'first',
      createCompleted: false,
      type: 'child',
      validate: false,
      detaileLoading: false,
      hasDefaultParentShortCode: false, // 是否携带了默认的 parent short code
    };
  },
  provide() {
    return {
      accountAssignInfo: this.assignInfo,
    };
  },
  methods: {
    goViewDetail() {
      let data = {
        organizationId: this.organizationId,
        identityId: this.organizationId,
        propShortCode: this.identityInfo.shortCode,
        cpId: this.cpId,
      };
      this.$router.push({
        path: 'detail/info',
        query: { data: encodeURIComponent(JSON.stringify(data)) },
      });
    },
    continueAdd() {
      this.refreshCurrentPage();
    },
    resetFields() {
      // reset fields
      this.$refs.identityTab.resetFields();
      this.$refs.assignInfoTab.resetFields();
      // special fields reset
      this.createCompleted = false;
      this.activeName = 'first';
      this.cpId = '';
      this.organizationId = '';
    },
    refreshAccountTypeEvn() {
      this.loadQueryAccountType();
    },

    // 刷新AccountType
    loadQueryAccountType() {
      if (this.assignInfo.products.length > 0) {
        let productIds = [];
        this.assignInfo.accountTypeRuleProfileBeanList = [];
        this.assignInfo.products.forEach((item) => {
          productIds.push(item.productId);
        });
        let params = {
          productIds: productIds,
          trustLevelId: this.assignInfo.trustLevelId,
          segmentIds: this.assignInfo.segmentIds,
        };
        queryAccountType(params).then((data) => {
          this.assignInfo.accountTypeRuleProfileBeanList = data.accountTypeRuleProfileBeanList;
        });
      }
    },
    // 验证Kyc的值
    getCheckInputKycValue(params1, params2) {
      this.submitLoading = true;
      checkInputKyc(params1)
        .then((res) => {
          this.submitLoading = false;
          if (res.header && res.header.success && res.result) {
            this.getTrustLevelAndSegment(params2);
          }
        })
        .finally(() => {
          this.submitLoading = false;
        });
    },

    // 获取第二页信息
    getTrustLevelAndSegment(param) {
      this.submitLoading = true;
      trustLevelAndSegment(param)
        .then((res) => {
          this.submitLoading = true;
          if (res.header && res.header.success) {
            this.assignInfo.defaultChargeProfileId = parseInt(res.defaultChargeProfileId);
            this.assignInfo.defaultRuleProfileId = parseInt(res.defaultRuleProfileId);
            this.assignInfo.selectChargeProfileId = parseInt(res.selectChargeProfileId);
            this.assignInfo.selectedRuleProfileId = parseInt(res.selectedRuleProfileId);
            this.assignInfo.chargeProfileList = res.chargeProfileList;
            this.assignInfo.ruleProfileList = res.ruleProfileList;
            this.assignInfo.products = res.products.map((item) => {
              item.effectiveDateInit = item.effectiveDate;
              item.expireDateInit = item.expireDate;
              item.isProduct = 0;
              return item;
            });
            this.assignInfo.selectSegmentName = res.selectSegmentName;
            this.assignInfo.trustLevel = res.trustLevel;
            this.assignInfo.trustLevelId = res.trustLevelId;
            this.assignInfo.trustLevelName = res.trustLevelName;
            this.assignInfo.segmentIds = res.segmentIds;
            this.assignInfo.accountTypeRuleProfileBeanList = res.accountTypeRuleProfileBeanList.map((item) => {
              item.ruleProfileBeans = item.ruleProfileBeans || [];
              return item;
            });
            this.activeName = 'second';
          }
        })
        .finally(() => {
          this.submitLoading = false;
        });
    },
    next() {
      if (this.activeName === 'first') {
        let params1 = {
          trustLevelName: this.identityInfo.trustLevelName,
          identityKYCData: this.$refs.identityTab.getIdentityKYCData(),
          identitySubType: '5002',
        };
        let params2 = {
          selectBasicProductId: this.identityInfo.basicProductId,
          identityKYCData: this.$refs.identityTab.getIdentityKYCData(),
          identitySubType: '5002',
        };
        this.$refs.identityTab.validate().then((resultTag) => {
          if (resultTag) {
            this.$refs.identityTab.$refs.OrgKycInfo.validate().then((resultTag) => {
              if (resultTag) {
                this.getCheckInputKycValue(params1, params2);
              }
            });
          }
        });
      } else if (this.activeName === 'second') {
        if (this.assignInfo.products.filter((item) => !item.effectiveDate || !item.expireDate).length == 0) {
          this.$refs.assignInfoTab.validate().then((resultTag) => {
            if (resultTag) {
              this.$refs.assignInfoTab.$refs.commissionSettlementInfo.validate().then((resultTag) => {
                if (resultTag) {
                  this.activeName = 'third';
                }
              });
            }
          });
        }
      }
    },
    previous() {
      if (this.activeName === 'second') {
        this.activeName = 'first';
      } else {
        this.activeName = 'second';
      }
    },
    cancel() {
      this.$confirm('Are you sure to reset all data you have  entered/selected  in this page?', '', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        this.$refs.identityTab.deleteIdentifier(this.identityInfo.shortCode).then(() => {
          this.refreshCurrentPage();
        });
      });
    },
    //  提交数据前将  revenueSettlement  balance类的去掉逗号
    removeCommas(arr) {
      if (!arr || !Array.isArray(arr)) {
        return;
      }
      for (let i = 0; i < arr.length; i++) {
        for (const key in arr[i]) {
          if (
            ['settlementMinBalance', 'triggerBalance', 'targetBalance'].includes(key) &&
            typeof arr[i][key] === 'string'
          ) {
            let currentVal = arr[i][key];
            arr[i][key] = currentVal.replaceAll(',', '');
          } else if (Array.isArray(arr[i][key])) {
            this.removeCommas(arr[i][key]);
          }
        }
      }
    },
    submit() {
      // 提交的时候去除逗号
      let orgRevenueProfile = null;
      const profile = this.assignInfo.revenueFormData;
      if (profile && profile.profileId) {
        orgRevenueProfile = JSON.parse(JSON.stringify(profile));
        this.removeCommas(orgRevenueProfile.settlementProfileList);
      }
      let param = {
        parentId: this.identityInfo.parentId,
        parentShortCode: this.identityInfo.parentShortCode,
        shortCode: this.identityInfo.shortCode,
        merchName: this.identityInfo.merchName,
        notificationChannel: this.identityInfo.notificationChannel,
        language: this.identityInfo.language,
        email: this.identityInfo.email,
        phoneNumber: this.identityInfo.phoneNumber,
        identityModel: this.assignInfo.identityModel,
        aggregatorModel: this.assignInfo.aggregatorModel,
        centralOwnedModel: this.assignInfo.centralOwnedModel,
        bankAccounts: this.assignInfo.bankAccounts,
        orgRevenueProfile,
        commiDistributionModelId: this.assignInfo.profileFormTemp.subAgentExpansionModel,
        settlementConfigTemplate: this.assignInfo.profileFormTemp.settlementConfigTemplate,
        type: this.type,
        trustLevel: this.assignInfo.trustLevel,
        identityKYCData: this.$refs.identityTab.getIdentityKYCData(),
        accountTypeRuleProfileBeanList: this.assignInfo.accountTypeRuleProfileBeanList,
        productBeans: this.assignInfo.products,
        segmentIdList: this.assignInfo.segmentIds || [],
        trustLevelId: this.assignInfo.trustLevelId,
        notifyText: this.assignInfo.validationNotificationText,
        subAgentExpansionModel: this.assignInfo.subAgentExpansionModelTwo,
        profile: this.revenueFormData,
        identityRuleProfileId: this.assignInfo.selectedRuleProfileId,
        chargeProfileId: this.assignInfo.selectChargeProfileId,
        payBillCheckRule: this.assignInfo.showBillReferenceNumberValidation
          ? {
              validationMode: this.assignInfo.validationMode,
              validationExpression: this.assignInfo.validationExpression,
              allowPartialPayement: this.assignInfo.allowPartialPayement ? '0' : '1',
              notifyText: this.assignInfo.notifyText,
            }
          : null,
      };

      if (this.assignInfo.showBillReferenceNumberValidation) {
        param.payBillCheckRule = {
          validationMode: this.assignInfo.validationMode,
          validationExpression: this.assignInfo.validationExpression,
          allowPartialPayement: this.assignInfo.allowPartialPayement,
          notifyText: this.assignInfo.notifyText,
        };
      }
      this.submitLoading = true;

      addOrganization(param)
        .then((res) => {
          this.submitLoading = false;
          if (res.header && res.header.success) {
            this.createCompleted = true;
            this.organizationId = res.identityId;
            this.cpId = res.cpId;
          }
        })
        .finally(() => {
          this.submitLoading = false;
        });
    },
    getOrgNameRegex() {
      this.detaileLoading = true;
      queryOrgNameRegex({ identitySubType: '5002' })
        .then((res) => {
          if (res.header && res.header.success) {
            this.identityInfo.orgNameRegex = res.orgNameRegex;
          }
        })
        .finally(() => {
          if (!this.identityInfo.orgNameRegex) {
            this.identityInfo.orgNameRegex = '^([0-9A-Za-z\\_\\^\\|\\/ ]{2,128})$';
          }
          this.detaileLoading = false;
        });
    },
  },
  computed: {
    styles() {
      return {
        height: parseInt(document.documentElement.clientHeight / 2) + 'px',
      };
    },
  },
  watch: {
    'identityInfo.selectedProdList': {
      handler(newVal) {
        this.assignInfo.showBillReferenceNumberValidation = newVal.some((ele) => {
          if (ele.productProperties) {
            return ele.productProperties.BILL_REF_NUM_RULE != undefined;
          }
        });
      },
      deep: true,
    },
  },
  created() {
    this.getOrgNameRegex();
    if (this.$route.query) {
      const {
        query: { parentShortCode, parentIdentityId },
      } = this.$route;
      // 创建子组织时，携带了默认的 parent org 信息
      if (parentShortCode) {
        this.hasDefaultParentShortCode = true;
        this.identityInfo.defalutParentShortCode = parentShortCode;
        this.identityInfo.parentShortCode = parentShortCode;
        this.identityInfo.parentIdentityId = parentIdentityId;
      }
    }
  },
  mounted() {
    window.v = this;
  },
};
</script>
<style lang="scss" scoped>
