  }

  /deep/ {
    .page-header {
      padding: 20px 20px 40px;
      min-height: unset;
    }

    .steps-tabs-container {
      .el-tabs__nav {
        transform: translateX(20px) !important;
      }
    }
    .el-step.is-horizontal .el-step__line {
      height: 1px;
      top: 10px;
    }

    .info-tabs-container {
      margin-top: 0 !important;
      .el-tabs__header {
        padding: 0 20px 20px;

        background-color: $base-menu-light-background;
      }
    }

    .el-step__icon {
      width: 18px;
      height: 18px;
      font-size: 14px;
    }

    .el-step__title {
      font-size: 14px;
      line-height: 28px;
    }
  }
}
</style>
