<template>
  <div class="identity-info">
    <el-form :model="identityInfo" ref="identityInfo" :rules="identityRules" label-position="top" size="small">
      <!-- product -->
      <simple-collapse
        class="panel-collapse has-subTitle margin-bottom-20"
        :title="$t('IDC.organization.product')"
        name="roleInfo"
      >
        <!-- title -->
        <div class="tips p-l-r-20" style="margin-top: 8px" v-if="!isViewMode">
          After a basic product is selected for an identity, the system displays the minimum trust level and mandatory
          KYC fields required for the product.
        </div>
        <div style="margin-top: 13px">
          <el-divider />
        </div>

        <!-- add / edit product -->
        <div class="p-l-r-20">
          <!-- add product -->
          <div
            id="addProd"
            class="add-prod el-form-item-with-btn"
            v-if="!isViewMode && this.identityInfo.selectedProdList.length === 0"
          >
            <el-form-item prop="selectedProdList" required>
              <el-button
                slot="label"
                icon="el-icon-plus"
                type="primary"
                plain
                size="small"
                @click="showProductPopup = true"
              >
                Add
              </el-button>
            </el-form-item>
          </div>
          <!-- edit product -->
          <div v-else>
            <div id="editProd" class="edit-prod el-form-item-with-btn" v-if="!isViewMode">
              <el-form-item prop="selectedProdList">
                <el-button
                  slot="label"
                  icon="el-icon-edit"
                  type="primary"
                  plain
                  size="small"
                  @click="showProductPopup = true"
                >
                  {{ $t('IDC.orgCustomer.edit') }}
                </el-button>
              </el-form-item>
            </div>

            <!--prodList edit -->
            <div class="sub-block__title" v-if="isViewMode">Product List</div>
            <!-- prodList create -->
            <table-page
              v-if="!isViewMode"
              ref="productListTable"
              size="small"
              style="margin-bottom: 20px"
              v-loading="prodLoading"
              :isSingleCheck="false"
              :has-pagination="false"
              :columns="productColumns"
              :outSideTableData="identityInfo.selectedProdList"
            >
              <template slot="trustLevel" slot-scope="scope">
                {{ `${scope.data.row.trustLevel || ''}:${scope.data.row.trustLevelName || ''}` }}
              </template>
            </table-page>
            <!--prodList review -->
            <table-page
              v-else
              size="small"
              style="margin-bottom: 20px"
              :has-pagination="false"
              :columns="productColumns"
              :outSideTableData="identityInfo.selectedProdList"
            >
              <template slot="trustLevel" slot-scope="scope">
                {{ `${scope.data.row.trustLevel || ''}:${scope.data.row.trustLevelName || ''}` }}
              </template></table-page
            >
          </div>
        </div>
      </simple-collapse>

      <!-- identityInfo -->
      <simple-collapse
        class="panel-collapse margin-bottom-20"
        :title="$t('IDC.organization.identityInfo')"
        name="identityInfo"
      >
        <el-divider />

        <!-- Basic Info -->
        <div class="p-l-r-20 p-b-6">
          <div class="sub-block__title">{{ $t('IDC.organization.basicInfo') }}</div>
          <el-row :gutter="20" class="el-row--flex flex-wrap">
            <!-- parentShortCode -->
            <el-col class="el-col-auto-span">
              <el-form-item
                prop="parentShortCode"
                ref="parentShortCode"
                :label="$t('IDC.organization.parentShortCode')"
                class="form-more-select-box el-form-item-parent-short-code"
              >
                <div v-if="isViewMode">
                  <span v-text="identityInfo.parentShortCode" />
                </div>
                <el-input
                  id="parentShortCode"
                  v-else
                  v-model="identityInfo.parentShortCode"
                  @change="onParentShortCodeChange"
                  maxlength="24"
                  :disabled="hasDefaultParentShortCode || !identityInfo.selectedProdList.length"
                  clearable
                >
                  <el-link
                    class="short-code-icon el-icon-more__suffix--cursor"
                    icon="el-icon-more"
                    :underline="false"
                    slot="append"
                    @click="shortCodeDialogVisible = true"
                    v-if="identityInfo.selectedProdList.length && !identityInfo.defalutParentShortCode"
                  ></el-link>
                  <el-link
                    class="short-code-icon el-icon-more__suffix--disabled"
                    icon="el-icon-more"
                    :underline="false"
                    slot="append"
                    v-else
                  ></el-link>
                </el-input>
              </el-form-item>
            </el-col>
            <!-- parentOrgName -->
            <el-col class="el-col-auto-span">
              <el-form-item
                prop="parentOrgName"
                ref="parentOrgName"
                :label="$t('IDC.organization.parentOrganizationName')"
                :required="!isViewMode"
                :show-message="false"
              >
                <div v-if="isViewMode">
                  <span v-text="identityInfo.parentOrgName" />
                </div>
                <el-input v-else id="parentOrgName" v-model="identityInfo.parentOrgName" disabled></el-input>
              </el-form-item>
            </el-col>
            <!-- shortCode -->
            <el-col class="el-col-auto-span">
              <el-form-item
                prop="shortCode"
                ref="shortCode"
                :label="isViewMode ? $t('IDC.organization.shortCode') : ''"
              >
                <div v-if="isViewMode">
                  <span v-text="identityInfo.shortCode" />
                </div>
                <div class="el-form-item-double-lable" v-else>
                  <div class="el-form-item-double-lable__group">
                    <label slot="label" class="el-form-item__label el-form-item__label-left el-form-item__label-req">{{
                      $t('IDC.organization.shortCode')
                    }}</label>
                    <label
                      class="el-form-item__label el-form-item__label-right cusor-pointer"
                      @click="generateIdentifierByBtn"
                      v-if="identifierBtnisShow"
                      >{{ $t('IDC.organization.generateAnotherIdentifier') }}</label
                    >
                  </div>
                  <el-input
                    id="shortCode"
                    v-model.trim="identityInfo.shortCode"
                    placeholder="Please enter"
                    maxlength="24"
                    clearable
                  />
                </div>
              </el-form-item>
            </el-col>
            <!-- organizationName -->
            <el-col class="el-col-auto-span">
              <el-form-item prop="merchName" ref="merchName" :label="$t('IDC.organization.organizationName')">
                <div v-if="isViewMode">
                  <span v-text="identityInfo.merchName" />
                </div>
                <el-input
                  id="merchName"
                  v-else
                  v-model="identityInfo.merchName"
                  :placeholder="$t('common.placeholder.enter')"
                  maxlength="256"
                  clearable
                />
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </simple-collapse>

      <!-- KYC info -->
      <simple-collapse class="panel-collapse" :title="$t('IDC.organization.kycInfo')" name="kycInfo">
        <el-divider />
        <div class="org-kyc-form">
          <kyc-form
            ref="OrgKycInfo"
            identityClassify="org"
            :kycInfo.sync="kycFormData"
            :identityType="5002"
            :mode="`${isViewMode ? 'review' : 'create'}`"
            :reviewKycData="kycInfo"
            :adaptiveLayout="true"
            :basicProductId="identityInfo.basicProductId"
          />
        </div>
      </simple-collapse>
    </el-form>

    <!-- parentShortCode选择弹窗 -->
    <portal-short-code
      :searchVisible.sync="shortCodeDialogVisible"
      :short-code="identityInfo.parentShortCode"
      @selected-data="fillSelectedData"
      :initializingLoading="false"
    ></portal-short-code>

    <!-- addProduct弹窗 -->
    <portal-add-popup
      :searchVisible.sync="showProductPopup"
      v-if="!isViewMode"
      :isCheck="true"
      :isSingleCheck="true"
      :isCheckMemory="true"
      :isShowSearch="true"
      :hasPagination="true"
      :addForm="productPopupConf"
      :init-checks="identityInfo.selectedProdList"
      :childSearch="getProduct"
      @submit-call-back="addProduct"
      ><template slot="trustLevel" slot-scope="scope">
        {{ `${scope.row.trustLevel}:${scope.row.trustLevelName}` }}
      </template>
    </portal-add-popup>
  </div>
</template>

<script>
import {
  loadLanguageList,
  queryProductList,
  checkIdentifierBtn,
  generateIdentifier,
  deleteIdentifier,
} from '@/views/identifier-center/api/api-organization.js';

import { getIdentityInfoRules } from '@/views/identifier-center/validator/organization';
import KycForm from '@/components/kyc-common/kyc-form';
import SimpleCollapse from '../components/simple-collapse.vue';
import TablePage from '@/components/tablePage';
import portalShortCode from '@/components/portal-short-code/index.vue';
import PortalAddPopup from '@/components/portal-add-popup/index';

const IDENTITY_TYPE_ORGANIZATION = 'Organization';

export default {
  name: 'IdentityInfo',
  components: { TablePage, SimpleCollapse, portalShortCode, PortalAddPopup, KycForm },
  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    type: {
      type: String,
      default: 'top',
    },
    identityInfo: {
      type: Object,
      default: () => {},
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
    kycInfo: {
      type: Object,
      default: () => {},
    },
    hasDefaultParentShortCode: Boolean,
  },
  data() {
    return {
      kycFormData: {},
      basicProductId: '',
      showProductPopup: false,
      productPopupConf: {
        title: 'Add Product',
        label: 'Product Name',
        idKey: 'productId',
        columns: [
          { label: 'Product Name', prop: 'productName', align: 'left' },
          { label: 'Trust Level', prop: 'trustLevel', align: 'left', __slotName: 'trustLevel' },
          { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left' },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: 'Product Name',
              key: 'name',
              prop: 'name',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      shortCodeDialogVisible: false,

      prodLoading: false,
      identifierBtnisShow: false,

      productColumns: [
        { label: 'Product Name', prop: 'productName', align: 'left' },
        { label: 'Trust Level', prop: 'trustLevel', align: 'left', __slotName: 'trustLevel' },
        { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left' },
      ],

      identityRules: !this.isViewMode ? getIdentityInfoRules(this.identityInfo, this.assignInfo) : {},
      languageOptions: [],
      channelOptions: [],
    };
  },
  created() {
    this.firstCheckIdentitiferBtn();
    // 初始化语言下拉列表
    this.initLanguage();
  },

  watch: {
    kycFormData: {
      handler(value) {
        if (value) {
          this.$emit('update:kycInfo', JSON.parse(JSON.stringify(value)));
        }
      },
      deep: true,
    },
  },

  mounted() {
    window.addEventListener('beforeunload', this.onBeforeWindowClosed);
    if (this.identityInfo.defalutParentShortCode) {
      this.onParentShortCodeChange(this.identityInfo.defalutParentShortCode);
    }
  },

  methods: {
    // 获取identityKYCData
    getIdentityKYCData() {
      return this.$refs.OrgKycInfo.getKYCRequestParams();
    },

    fillSelectedData(value) {
      this.identityInfo.parentShortCode = value;
      this.onParentShortCodeChange(value);
      this.identityRules = !this.isViewMode ? getIdentityInfoRules(this.identityInfo, this.assignInfo) : {};
    },

    validateRoleAndProduct() {
      this.validateIdenFormFields(['selectedProdList']);
    },

    validateIdenFormFields(fields) {
      this.$refs['identityInfo'].validateField(fields);
    },

    // 点击Confirm按钮调用
    addProduct(data) {
      if (this.identityInfo.selectedProdList.length && this.identityInfo.parentShortCode) {
        this.identityRules = !this.isViewMode ? getIdentityInfoRules(this.identityInfo, this.assignInfo) : {};
      }
      this.identityInfo.basicProductId = data.productId; // 改变basicProductId触发changeBasicProduct接口改变kyc必填项
      this.identityInfo.trustLevelName = data.trustLevelName;
      this.assignInfo.basicProductUniqueId = data.uniqueId;
      let list = [];
      if (!data) {
        list = [];
      }
      if (Array.isArray(data)) {
        list = data;
      }
      data.isProduct = 0;
      list = list.concat(data);
      this.identityInfo.selectedProdList = list;
    },

    // 点击add调用
    async getProduct(param) {
      return new Promise((resolve) => {
        this.prodLoading = true;
        let params = {
          pageNo: param.pageNo || 1,
          identitySubType: 5002,
        };
        if (param.searchKey) {
          params.productName = param.searchKey.name;
        }
        if (this.identityInfo.defalutParentShortCode) {
          params.parentShortCode = this.identityInfo.defalutParentShortCode;
          params.identityId = this.identityInfo.parentIdentityId;
        }
        queryProductList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products;
              res.total = res.page.totalSize;
            }
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {
            this.prodLoading = false;
          });
      });
    },

    initLanguage() {
      loadLanguageList().then((res) => {
        if (res.header && res.header.success) {
          this.languageOptions = res.list || [];
        }
      });
    },

    getLanguageName(languageCode) {
      let item = this.languageOptions.find((item) => item.languageCode === languageCode);
      return item ? item.languageName : languageCode;
    },

    validateProd() {
      let flag = true;
      if (this.identityInfo.selectedProdList.length === 0) {
        flag = false;
      }
      return flag;
    },

    validate() {
      return new Promise((resolve) => {
        let promObj = new Promise((resolve) => {
          this.$refs['identityInfo'].validate((valid) => {
            return resolve(valid);
          });
        });
        let productFlag = this.validateProd();
        promObj.then((identityFlag) => {
          return resolve(identityFlag && productFlag);
        });
      });
    },

    clearValidate(fields) {
      window.requestIdleCallback(() => {
        this.$refs['identityInfo'].clearValidate(fields);
      });
    },

    resetFields() {
      this.$refs['identityInfo'].resetFields();
      this.identityInfo.selectedProdList = [];
    },

    async onParentShortCodeChange(val) {
      if (!val) {
        return;
      }

      // 1 validate parentShortCode 才能触发 parentId被赋值
      this.validateIdenFormFields(['parentShortCode']);
      // 2 check generate btn
      const params = {
        identityType: IDENTITY_TYPE_ORGANIZATION,
        type: 'child',
        parentCode: val,
      };

      const hasRule = await this.checkIdentifierBtn(params);
      if (!hasRule) {
        this.identifierBtnisShow = false;
        return;
      } else {
        this.identifierBtnisShow = true;
      }
    },

    deleteIdentifier(identifier) {
      if (!identifier) {
        return Promise.resolve(false);
      }
      return deleteIdentifier({
        identifier: identifier,
        identityType: IDENTITY_TYPE_ORGANIZATION,
      })
        .then((res) => {
          if (res.header && res.header.success) {
            return true;
          }
          return false;
        })
        .catch(() => {
          return false;
        });
    },

    checkIdentifierBtn(data) {
      return checkIdentifierBtn(data)
        .then((res) => {
          if (res.header && res.header.success) {
            return res.hasRule === true;
          }
          return false;
        })
        .catch(() => {
          return false;
        });
    },

    generateIdentifier(data) {
      return generateIdentifier(data)
        .then((res) => {
          if (res.header && res.header.success) {
            return res.identifier;
          }
          return false;
        })
        .catch(() => {
          return false;
        });
    },

    async generateIdentifierByBtn() {
      // hasRule 展示按钮 并 生成 code
      const generParmas = {
        identityType: IDENTITY_TYPE_ORGANIZATION,
        preCode: this.identityInfo.shortCode,
        parentCode: this.identityInfo.parentShortCode,
        isBtnTrigger: true,
      };
      const code = await this.generateIdentifier(generParmas);
      if (code !== '' && !code) {
        return;
      }

      this.identityInfo.shortCode = code;
      this.clearValidate(['shortCode']);
    },
    async firstCheckIdentitiferBtn() {
      const params = {
        identityType: IDENTITY_TYPE_ORGANIZATION,
        type: 'child',
        parentCode: null,
      };

      const hasRule = await this.checkIdentifierBtn(params);
      if (!hasRule) {
        this.identifierBtnisShow = false;
        this.identityInfo.shortCode = '';
        return;
      }
      // hasRule 展示按钮 并 生成 code
      const generParmas = {
        identityType: IDENTITY_TYPE_ORGANIZATION,
        preCode: '',
        parentCode: null,
        isBtnTrigger: false,
      };
      const code = await this.generateIdentifier(generParmas);
      if (code !== '' && !code) {
        return;
      }
      this.identifierBtnisShow = true;
      this.identityInfo.shortCode = code;
      this.clearValidate(['shortCode']);
    },
    onBeforeWindowClosed() {
      this.deleteIdentifier(this.identityInfo.shortCode);
    },
  },
};
</script>
